// 批量删除

// 获取tab切换当前的index
const getTabIndex = index => {
	tabIndex.value = index;
   // 根据不同的tab值切更新 取件数据
	if (index === 0) {
		list.value.dealPList();
	} else if (index === 1) {
		list.value.alreadList();
	} else {
		list.value.cancelList();
	}
	selected.clear();
	// 修改底部管理按钮状态，因为取件、派件公用了一个底部管理组件，因此切换tab的时候先把isAdmin设置成false，以防数据混搅。
	isAdmin.value =false
	
};
// 触发选项卡事件
