	tab.value.changeTab(e.detail.current);
};
// 获取foot底部组件的管理按钮触发值，向列表页传递，用来控制全选，单选功能
const getAdmin = val => {
	isAdmin.value = val;
};
// 给筛选组件传递，刷新列表
const getList = () => {
	list.value.dealPList();
};

